$color-primary: #3d200d;
$color-secondary: #8e4d22;
$color-tertiary: #e9e7df;

* {
  margin: 0;
  padding: 0;
  font-size: 10px;
  box-sizing: border-box;
  color: $color-primary;
}

.u {
  &__space-around {
    &--col {
      justify-content: space-evenly;
      display: flex;
      flex-direction: column;
      align-content: flex-end;
    }
    /* Smartphones (portrait and landscape) ----------- */
    @media only screen and (max-device-width: 450px) {
      // display: grid;
      width: 100%;
    }
  }

  &__container {
    font-size: 1.8em;
    min-height: 3.22em;
    font-family: "DM Sans", sans-serif;
    margin: 0.4em auto 0 auto;
    border-radius: 5px;
    background-color: #fff;

    &--white {
      display: flex;
      align-items: center;
      justify-content: center;
      grid-area: price;

      /* Smartphones (portrait and landscape) ----------- */
      // @media only screen and (max-device-width: 450px) {
      width: 100%;
      min-height: 20px;
      font-size: 1.2em;
      // }
    }
    &--tall {
      margin: 0.5em auto 0 auto;
      display: grid;
      // justify-content: flex-start;
      // align-items: flex-start;
      // flex-direction: column;
      padding: 1em;

      /* Smartphones (portrait and landscape) ----------- */
      @media only screen and (max-device-width: 450px) {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        width: 100%;
        min-height: 20px;
        padding: 0;
        background-color: transparent;
      }
      & > li {
        width: 100%;
        text-align: left;
        padding: 0 1em;
        display: flex;
        justify-content: space-between;
      }

      & img {
        float: left;
        height: 5em;
        width: auto;
      }
    }
  }
}

.TnC {
  max-width: 75em;
  overflow: hidden;
  height: 100%;
  margin: 0 auto;
  display: grid;
  // grid-template-columns: 44.7em 60em auto;
  // grid-template-areas: "aside1 map-area aside2";
  grid-template-columns: 100%;
  grid-template-areas: "aside1" "aside2" "map-area";

  /* iPads (portrait) ----------- */
  // @media only screen and (max-device-width: 1024px) {
  //   grid-template-columns: 60% 40%;
  //   grid-template-areas: "aside1 aside1" "map-area aside2";
  // }

  /* Smartphones (portrait and landscape) ----------- */
  @media only screen and (max-device-width: 450px) {
    grid-template-columns: 100%;
    grid-template-areas: "aside1" "aside2" "map-area";
  }

  &__aside {
    position: relative;
    width: 100%;

    // @media only screen and (max-device-width: 1024px) {
    //   width: 100%;
    // }
    &--left {
      grid-area: aside1;
      padding: 25px 6em;
      background: #e9e7df;
      display: grid;
      grid-template-columns: 24em auto;
      height: 24em;
      & :nth-child(2) {
        justify-items: baseline;
      }
      grid-template-areas: "left-logo left-para1" "left-logo left-para2";

      // @media only screen and (max-device-width: 1024px) {
      //   grid-template-columns: 24em auto;
      //   grid-template-areas: "aside1 aside1" "map-area aside2";
      //   padding: 25px 2em;
      //   grid-template-rows: 25em;
      //   height: auto;
      // }

      /* Smartphones (portrait and landscape) ----------- */
      @media only screen and (max-device-width: 450px) {
        grid-template-columns: 13em auto;
        grid-template-areas: "left-logo left-para1" "left-para2 left-para2";

        // grid-template-areas: "t1 t2" "b1 b1";
        // grid-template-rows: 24em auto;
        padding: 20px 1em;
        height: auto;
      }
    }
    &--bottom {
      padding: 25px 6em;
      background: #e9e7df;
      height: 100px;
      // display: none;

      /* Smartphones (portrait and landscape) ----------- */
      @media only screen and (max-device-width: 450px) {
        text-align-last: center;
        display: block;
      }
    }
    &--right {
      grid-area: aside2;
      padding: 4em 0;
      display: flex;
      flex-flow: column;
      align-items: stretch;
      // @media only screen and (max-device-width: 1024px) {
      // width: 100% !important;
      //   padding-right: 20px;
      // }
      /* Smartphones (portrait and landscape) ----------- */
      @media only screen and (max-device-width: 450px) {
        padding: 1em 2em;
      }
    }
  }

  &__HeadingContainer {
    background-image: url("./assets/SectionGraphics2.svg");
    // background-size: auto 30em;
    background-repeat: no-repeat;
    margin-left: -6em;
    grid-area: left-logo;

    /* Smartphones (portrait and landscape) ----------- */
    @media only screen and (max-device-width: 450px) {
      grid-area: left-logo;
      padding-bottom: 20px;
      background-size: auto 13em;
      height: 15em;
      margin-left: -7em;
    }
  }

  &__LogoGraphics {
    text-align: center;
    &--left {
      width: auto;
      /* Tablet (portrait and landscape) ----------- */
      // @media only screen and (max-device-width: 1024px) {
      //   float: right;
      // }
      /* Smartphones (portrait and landscape) ----------- */
      // @media only screen and (max-device-width: 450px) {
      display: none;
      // }
    }
  }

  &__paragraph {
    &--left1 {
      color: $color-primary;
      font-family: "DM Serif Display", serif;
      font-size: 2.1em;
      line-height: 1.167em;
      grid-area: left-para1;

      /* Smartphones (portrait and landscape) ----------- */
      @media only screen and (max-device-width: 450px) {
        font-size: 2em;
        height: 13em;
      }
    }
    &--left2 {
      grid-area: left-para2;
      color: $color-secondary;
      font-family: "DM Sans", sans-serif;
      font-size: 1.2em;
      line-height: 1.55em;
      @media only screen and (max-device-width: 450px) {
        font-size: 1.5em;
        position: absolute;
        bottom: 0;
        left: 0;
        text-align: center;
      }
    }
  }

  &__heading {
    position: absolute;
    left: -1000000em;
  }
}

.TnC-map {
  grid-area: map-area;
  position: relative;
  padding: 5em;
  padding-top: 0;
  @media only screen and (max-device-width: 450px) {
    padding: 1em 2em;
    font-size: 1em;
  }

  &__wrapper {
    display: flex;
    flex-flow: column wrap;
    justify-content: space-evenly;
    border: 2px solid $color-secondary;
    border-radius: 10px;
    height: 100%;
    padding: 2em;

    @media only screen and (max-device-width: 450px) {
      padding: 0.5em;
      font-size: 1em;
    }
  }
  &__heading {
    font-family: "DM Serif Display", serif;
    font-size: 2.1em;
    line-height: 1.381em;
    font-weight: normal;
    text-align: center;
    text-transform: uppercase;
    color: $color-primary;
    @media only screen and (max-device-width: 450px) {
      font-size: 1.6em;
    }
  }
  &__subheading {
    color: $color-secondary;
    font-family: DM Sans;
    font-weight: normal;
    font-style: normal;
    font-size: 1em;
    line-height: 1.8em;
    text-align: center;
    text-transform: uppercase;
    padding-top: 1em;
  }
  &__chart-area {
    display: grid;
    grid-template-columns: 70px auto;
    position: relative;
    margin: 0 auto;

    @media only screen and (max-device-width: 450px) {
      grid-template-columns: auto;
    }
  }
  &__map-path:hover {
    fill: rgb(0, 255, 255);
  }

  &__svg {
    width: 100%;
    height: 52em;
  }
  &__brands {
    margin: 1em 0;
    display: grid;
    width: 80%;
    grid-template-columns: repeat(7, 1fr);

    @media only screen and (max-device-width: 450px) {
      // display: grid;
      width: 90%;
      margin: 1em auto;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-template-rows: 1fr 1fr;
      grid-gap: 1em;
      align-items: center;
    }
  }
  &__brand-logo {
    border-radius: 5px;
    opacity: 0.2;
    cursor: pointer;
    height: 5.2em;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    transition: 0.4s;
    &--static {
      grid-area: logo;
      transition: none;
      margin: 0 auto;
      height: 5.3em;
    }
  }

  &__brand-logo:hover {
    transform: translateY(-2px);
    opacity: 0.8;
  }

  // &__brand-logo:not(:nth-child(1)) {
  //   margin-left: 1em;
  //   mix-blend-mode: normal;
  // }

  &__legend {
    width: 100%;
    &-container {
      @media only screen and (max-device-width: 450px) {
        display: none;
      }
    }
    // transform: translate(10, 30);
  }

  &__legend-text {
    @media only screen and (max-device-width: 450px) {
      display: none;
    }
    text-anchor: middle;
    font-family: "DM Sans", sans-serif;
    font-size: 1em;
    fill: $color-primary;
    font-weight: normal;
    &--upper,
    &--lower {
      font-weight: bold;
      text-anchor: start;
    }
  }
}

.TnC-aside {
  border-radius: 1em;
  // width: 33.8em;
  // margin-bottom: 2em;
  width: 65em;
  margin: 0 auto;
  @media only screen and (max-device-width: 450px) {
    width: 100%;
  }

  &__section-right {
    text-align: center;
    background-color: $color-tertiary;
    padding: 2rem;
    text-align: center;
    width: 100%;
    // @media only screen and (max-device-width: 1024px) {
    //   width: auto;
    // }

    &--2 {
      margin-top: 10px;
      flex: 1;
    }
    &--1 {
      min-height: 8em;

      & select {
        width: 100%;
        height: 2.3em;
        background: #ffffff;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 5px;
        font-size: 1.8em;
        padding: 0 1em;
        appearance: none;
        -webkit-appearance: none;
        -moz-appearance: none;
        background-position: right 10px top 50%;
        background-repeat: no-repeat;
      }
      & option {
        font-size: 1em;
      }
    }
  }

  &__selected-place--name {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 2em;
    // padding: 1em 2em;
    border-radius: 5px;
    font-weight: normal;
    font-size: 1.8em;
    color: #ffffff;
    font-family: "DM Sans", sans-serif;
    // width: 100%;
    text-align: center;
    background-color: $color-primary;
    margin: 0 17.7px 1em 17.7px;
  }

  &__selected-place--attr {
    // height: 2em;
    font-family: "DM Sans", sans-serif;
    font-size: 1.4em;
    color: $color-primary;
    font-weight: bold;

    /* Smartphones (portrait and landscape) ----------- */
    @media only screen and (max-device-width: 450px) {
      font-size: 1.2em;
    }

    &:nth-child(4) {
      @media only screen and (max-device-width: 450px) {
        font-size: 1.6em;
      }
    }
  }
  & &__icon {
    padding: 3.4em 0;
  }
}

.select-box {
  display: flex;
  width: 400;
}

.select-box .options-container {
  background: #2f2f2f;
  color: #f5f6fa;
  max-height: 0;
  width: 100%;
  opacity: 0;
  transition: 0.4s;
}

.active-brand {
  opacity: 1;
}

.top-three {
  // width: 100%;
  display: flex;
  justify-content: stretch;
  align-items: center;
  @media only screen and (max-device-width: 450px) {
    display: flex;
    flex-flow: column wrap;
    padding: 0;
    margin: 0;
  }

  // grid-auto-flow: column;

  // padding:0 1em;

  /* Smartphones (portrait and landscape) ----------- */
  & > .TnC-map__brand-logo--static {
    // @media only screen and (max-device-width: 450px) {
    margin: 0 auto;
    // }
  }

  &__text {
    border-bottom: 1.6px solid $color-primary;
    // padding: 10px;
    width: 100%;
    margin: 10px;
    text-align: center;
    flex: 1;
    display: flex;
    flex-direction: column;
    @media only screen and (max-device-width: 450px) {
      border: none;
    }
    & > h5 {
      font-size: 1.5em;
      // line-height: 2em;
    }
    & > h6 {
      font-weight: normal;
      font-size: 1.2em;
      padding-bottom: 0.5em;
      & > span {
        font-size: 1.2em;
        font-weight: bold;
        // @media only screen and (max-device-width: 450px) {
        display: block;
        font-size: 1em;
        // }
      }
    }
  }
}

.attr-wrapper {
  // width: 65em;
  /* Smartphones (portrait and landscape) ----------- */
  // @media only screen and (max-device-width: 450px) {
  display: grid;
  grid-template-areas:
    "logo hn"
    "logo price";
  grid-template-columns: 25% 75%;
  text-align: left;
  margin: 4em auto;
  padding-right: 17.7px;
  // }
}

.legend2__bar {
  height: 16px;
  border-radius: 10px;
}

.legend2__text-box {
  display: flex;
  justify-content: space-between;
}

.legend2 {
  display: none;
  @media only screen and (max-device-width: 450px) {
    margin: 2em 0;
    display: block;
    font-family: "DM Sans", sans-serif;
  }
}

.brand-placeholder {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  border-radius: 5px;
  margin: 0.556em auto;
  height: 2.7778em;
  width: 2.77778em;
  font-size: 1.8em;
  background: $color-secondary;
}
.TnC__zoom-button {
  font-size: 2.4em;
  width: 1.5em;
  height: 1.5em;
  background-color: $color-tertiary;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  cursor: pointer;
  border: none;
  transition: all 0.2s;
  opacity: 0.6;

  &:focus,
  &:hover {
    outline: none;
  }

  &:hover {
    opacity: 1;
  }
  &:active {
    transform: translateY(2px);
  }
}

.TnC__svgContainer {
  position: relative;
}
.TnC_buttonContainer {
  display: grid;
  grid-template-rows: 1fr 1fr;
  row-gap: 1em;
  position: absolute;
  right: 0;
  top: 0;
}

.inside-col {
  display: grid;
  grid-template-columns: 6fr 4fr;
  grid-gap: 20px;
  padding-top: 4em;
  @media only screen and (max-device-width: 450px) {
    grid-template-columns: 1fr;
  }
}
